.App {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  /* align-items: center; */
  justify-content: center;
  text-align: center;
}

.calculator {
  display: flex;
  justify-content: center;
}
.volumeWrapper {
  position: absolute;
  top: 20px;
  right: 20px;
}

.volumeBtn {
  background-color: transparent;
  border: none;
  &:hover {
    cursor: pointer;
  }
}
